import api from "../../../utils/api.js";
import util from "../../../utils/util.js";
export default {
	data() {
		return {
			v:'', // 版本号
			order_find: [{
					img: "/static/center/find.png",
					text: "找料中",
				},
				{
					img: "/static/center/deliver.png",
					text: "待收货",
				},
				{
					img: "/static/center/receive.png",
					text: "待评价",
				},
				{
					img: "/static/center/evaluate.png",
					text: "已完成",
				}
			],
			order_fetch: [{
					img: "/static/center/shouhuo.png",
					text: "取料中",
				},
				{
					img: "/static/center/deliver.png",
					text: "待收货",
				},
				{
					img: "/static/center/receive.png",
					text: "待评价",
				},
				{
					img: "/static/center/evaluate.png",
					text: "已完成",
				}
			],
			contents: [{
					title: "我的地址",
				},
				{
					title: "在线客服",
				},
				{
					title: "我的礼券",
				},
				{
					title: "积分商城",
				},
				// {
				// 	title: "我是找料员",
				// },
				// {
				// 	title: "我是配送员",
				// }
			],
			avatar_path: "/static/footer_icon/2.1.png",
			balance: 0,
			marketing: 0,
			virtual: 0,
		};
	},
	onLoad() {
		
	},
	onShow() {
		let xx = uni.getStorageSync('user_name');
		if(uni.getStorageSync('user_name')!=''){
			// 获取用户信息
			this.getUserInfo();
			// 版本号
			this.$data.v = wx.getStorageSync('v');
		}
	},
	created() {
		// 获取用户信息
		this.getUserInfo();
		// 版本号
		this.$data.v = wx.getStorageSync('v');
	},
	computed:{
		nickName(){
			return this.$store.state.nickName;
		}
	},
	methods: {
		// 切换账户
		goPage(index){
			uni.reLaunch({
				url: '../index/index?menuFrom=' + index
			})
		},
		// 获取用户信息
		getUserInfo(){
			api.getUserInfo({
				method: "GET"
			}).then((res) => {
				console.log(res.data);
				if (res.code == 0 || res.code == 200) {
					this.$data.avatar_path = res.data.avatar_path;
					this.$data.balance = res.data.balance;
					this.$data.marketing = res.data.marketing;
					this.$data.virtual = res.data.virtual;
					this.$store.commit('updateNickName',res.data.nick_name);
					// console.log(res.data);
				}
			})
		},
		goSetting(){
			uni.navigateTo({
				url:"/pages/setting/setting?avatarPath="+this.$data.avatar_path
			})
		},
		//跳转下一个页面
		goNextPage(index) {
			//util.successTips('index->' + index);
			if(index == 0){
				uni.navigateTo({
					url:'../address/address'
				})
			}else if(index == 2){
				uni.navigateTo({
					url:'../giftCertificate/giftCertificate'
				})
				
			}else if(index == 3){
				// 去 web-view 积分商城
				uni.navigateTo({
				  url: "../integralmall/integralmall"
				})
			}else if(index == 4){
				uni.navigateTo({
					url: '../start/start?menuFrom=' + 1
				})
			}else if(index == 5){
				uni.navigateTo({
					url: '../start/start?menuFrom=' + 2
				})
			}
			
		},
		// 跳转订单页面
		goOrderPage(index, nav) {
			uni.setStorageSync('method', nav);
			uni.setStorageSync('status', index + 1);
			uni.redirectTo({
				url: '../index/index?from=2'
			});
		},
		// 跳转IM列表
		goChatList() {
			uni.navigateTo({
				url: '../chatList/chatList',
			})
		},
		// 修改头像
		changeAvatarPath() {
			let _this = this;
			uni.showModal({
				title: '提示',
				content: '是否修改头像',
				success: (res) => {
					if (res.confirm) {
						uni.chooseImage({
							count: 1,
							sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
							sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
							success: (res) => {
								// 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
								const access_token = wx.getStorageSync('access_token') || '';
								let data = {};
								data.file = '[object Object]';
								data.type = 'big';
								let timestamp = Date.parse(new Date());
								data.timestamp = timestamp;
								data.sign = util.makeSign(api.apiUrl + '/api/upload', data);
								data.deviceId = "wx";
								data.platformType = "2";
								data.versionCode = '4.0';
								// 上传图片，返回链接地址跟id,返回进度对象
								let uploadTask = wx.uploadFile({
									url: `${api.apiUrl}/api/upload`,
									filePath: res.tempFilePaths[0],
									name: 'file',
									header: {
										'content-type': 'multipart/form-data',
										'Accept': 'application/json',
										'Authorization': `Bearer ${access_token}`
									},
									formData: data,
									success: (res) => {
										console.log('图片上传');
										console.log(res);
										var ress = JSON.parse(res.data);
										if (200 === ress.code || 0 === ress.code) {
											_this.$data.avatar_path = ress.data;
											uni.setStorageSync('avatarPath', ress.data);
											api.memberAvatarPath({
												method: 'POST',
												data: {
													avatar_path: ress.data
												}
											}).then((res) => {
												util.successTips('图片上传成功');
											})

										} else {
											util.errorTips('上传错误');
										}

									},
									fail(err) {
										console.log(err)
									},
									complete() {

									}
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消')
					}
				}
			})
		},
		// 去余额充值页面
		goRecharge(index) {
			wx.navigateTo({
				url: '../recharge/recharge?index=' + index,
			})

		},
		goIn() {
			api.getInviteCode({}).then((res) => {
				if (res.code == 200 || res.code == 0) {
					if (res.data.status == 0) {
						uni.navigateTo({
							url: '../familyExplain/familyExplain?familyStatus=' + res.data.status,
						})
					} else if (res.data.status == 1) {
						uni.navigateTo({
							url: '../familyCenter/familyCenter',
						})
					} else {
						uni.navigateTo({
							url: '../family/family',
						})
					}
				}
			})
		
		},
		
		
	}
}
